import React, { useEffect, useRef } from "react";
import Product from "./Product";
import ScrollReveal from "scrollreveal";

const HomeProductSection = (props) => {
  const listProduct = useRef([]);
  useEffect(() => {
    ScrollReveal().reveal(listProduct.current, {
      delay: 200,
      duration: 500,
      opacity: 0,
      interval: 300,
    });
    return () => {};
  }, []);
  return (
    <section className="home__products container mt-5">
      <div className="my-4 fs__title fw-bold">Sản phẩm của chúng tôi</div>
      <div className="row mb-4">
        {[...Array(3)].map((_, idx) => {
          return <Product key={idx} ref={(el) => (listProduct.current[idx] = el)} />;
        })}
      </div>
      <div className="row">
        {[...Array(3)].map((_, idx) => {
          return <Product key={idx} ref={(el) => (listProduct.current[idx + 3] = el)} />;
        })}
      </div>
    </section>
  );
};

export default HomeProductSection;
