import React, { forwardRef } from "react";
import styled from "styled-components";

const Product = ({ className }, ref) => {
  return (
    <div className={`${className} col-4 rounded`} ref={ref}>
      <a href="#">
        <div className="home__products__card">
          <div className="home__products__img">
            <img
              src="https://www.topclass.com.vn/articles/wp-content/uploads/2021/01/Ly-Qui-Khanh-Tien-bac-khong-mua-duoc-tai-nang.jpg"
              alt="Era"
            />
          </div>
          <div className="p-3">
            <p className="fs__text fw-bold">Lorem ipsum dolor sit.</p>
            <p className="fs__details text-success">70.000đ</p>
          </div>
        </div>
      </a>
    </div>
  );
};

export default styled(forwardRef(Product))`
  .home__products {
    a {
      color: white;
      &:hover {
        color: ${(props) => props.theme.colorTextTitle};
      }
    }
    &__card {
      border-radius: 16px;
    }
    &__img {
      height: 200px;
      > img {
        border-radius: 16px;
        height: inherit;
        width: 100%;
        object-fit: cover;
      }
    }
  }
`;
